import React from 'react';
import "../less/base.less";
import "../less/time-range-picker-select.less";
import { weekMaps } from '../config/tbody.js';
/**
 * @desc 对被选中的日期排序，
 *       按iden排：星期一 ~ 星期日
 *       按time排：00:00~23:00
 */
var sort = function (curr, next) {
    if (curr.iden) {
        return curr.iden - next.iden;
    }
    // 对 00:00和00:30排序
    if (curr.substring(0, 2) === next.substring(0, 2)) {
        return curr.substring(3) - next.substring(3);
    }
    return curr.substring(0, 2) - next.substring(0, 2);
};
/**
   * @desc 合并times，将 [00:00, 01:00, 02:00]等不间隔的时间段合并
   *       如果带半小时，那么就需要 [00:00, 00:30, 01:00] 才能合并成[00:00, 01:00]
   */
var handleMergeTimes = function (hasHalfHour, times) {
    var mergeTimes = [[times[0]]];
    hasHalfHour ? handleMergeHalfHour(times, mergeTimes) : handleMergeHour(times, mergeTimes);
    return mergeTimes;
};
// 只有小时的数据合并
var handleMergeHour = function (times, mergeTimes) {
    times.forEach(function (item) {
        var lastMergeArr = mergeTimes.slice(-1)[0];
        var isNext = item.substring(0, 2) - lastMergeArr.slice(-1)[0].substring(0, 2) === 1;
        if (isNext) {
            lastMergeArr.push(item);
        }
        if (!isNext && item !== times[0]) {
            mergeTimes.push([item]);
        }
    });
};
// 带半小时的数据合并
var handleMergeHalfHour = function (times, mergeTimes) {
    times.forEach(function (item) {
        var lastMergeArr = mergeTimes.slice(-1)[0];
        // 00:00-00:30 或者 00:30 - 01:00
        // 小时*100 + 0或50，半小时转成50
        var lastMergeItem = lastMergeArr.slice(-1)[0];
        var itemNum = item.substring(0, 2) * 100 + (item.substring(3) === '30' ? 50 : 0);
        var lastMergeNum = lastMergeItem.substring(0, 2) * 100 + (lastMergeItem.substring(3) === '30' ? 50 : 0);
        var isNext = itemNum - lastMergeNum === 50;
        if (isNext) {
            lastMergeArr.push(item);
        }
        if (!isNext && item !== times[0]) {
            mergeTimes.push([item]);
        }
    });
    mergeTimes.forEach(function (item) {
        var hour = +item.slice(-1)[0].substring(0, 2);
        if (item.slice(-1)[0].substring(3) === '30') {
            hour > 8 ? item.push(hour + 1 + ":00") : item.push("0" + (hour + 1) + ":00");
        }
        else {
            hour > 8 ? item.push(hour + ":30") : item.push("0" + hour + ":30");
        }
    });
};
// 如果是只有小时的话，需要处理下
var fromat = function (last) {
    var hour = ~~last.substring(0, 2) + 1;
    return hour > 9 ? hour + ":00" : "0" + hour + ":00";
};
var WeekTimeRangeSelected = function (props) {
    var hasHalfHour = props.hasHalfHour, checkedDatas = props.checkedDatas, handleEmpty = props.handleEmpty;
    // 增加数据字段，方便展示
    var cacheChecked = checkedDatas || [];
    cacheChecked.sort(sort).map(function (item, index) {
        cacheChecked[index].week = weekMaps.get(item.iden);
        cacheChecked[index].mergeTimes = handleMergeTimes(hasHalfHour, item.times.sort(sort));
    });
    // 清除
    var handleClear = function () {
        handleEmpty();
    };
    return (React.createElement("tr", { className: "wtrp-time-range-selected" },
        React.createElement("td", { colSpan: 49, className: "wtrp-selected-td" },
            React.createElement("div", { className: "wtrp-clearfix" },
                checkedDatas.length === 0 ?
                    React.createElement("span", { className: "wtrp-fl tip-text" }, "\u53EF\u62D6\u52A8\u9F20\u6807\u9009\u62E9\u65F6\u95F4\u6BB5") :
                    React.createElement("span", { className: "wtrp-fl tip-text" }, "\u5DF2\u9009\u62E9\u65F6\u95F4\u6BB5"),
                React.createElement("a", { className: "wtrp-fr", onClick: handleClear }, "\u6E05\u7A7A\u9009\u62E9")),
            cacheChecked.map(function (item, i) {
                return (React.createElement("div", { className: "wtrp-selected-td__selected-time", key: i },
                    React.createElement("p", { className: "wtrp-flex wtrp-break" },
                        React.createElement("span", { className: "tip-text" },
                            item.week,
                            "\uFF1A"),
                        React.createElement("span", { className: "wtrp-flex-1" }, item.mergeTimes.map(function (time, timeIndex) {
                            return React.createElement("span", { className: "wtrp-selected-text", key: timeIndex }, hasHalfHour ? time[0] + "~" + time[time.length - 1] : time[0] + "~" + fromat(time[time.length - 1]));
                        })))));
            }))));
};
export default WeekTimeRangeSelected;
